.Organ-request-dashboard-container {
  max-width: 900px;
  margin: 2rem auto;
  background: var(--bg-white, #fff);
  border-radius: var(--radius-xl, 12px);
  box-shadow: var(--shadow-md, 0 4px 6px rgba(0,0,0,0.1));
  padding: 2rem 1.5rem 1.5rem 1.5rem;
}
.Organ-request-dashboard-container h2 {
  text-align: center;
  color: var(--accent, #3b82f6);
  margin-bottom: 1rem;
}
.dashboard-controls {
  display: flex;
  flex-wrap: wrap;
  gap: 1.25rem;
  align-items: center;
  margin-bottom: 1.5rem;
  justify-content: center;
}
.dashboard-controls label {
  font-weight: 500;
  color: var(--text-secondary, #6b7280);
  margin-right: 0.5rem;
}
.dashboard-controls select {
  min-width: 120px;
  font-size: 1rem;
  padding: 0.44rem 0.6rem;
  border-radius: var(--radius-md, 6px);
  background: var(--bg-gray-50, #f9fafb);
  border: 1px solid var(--border-light, #e5e7eb);
  margin-right: 0.8rem;
}
.Organ-requests-table {
  border-collapse: collapse;
  width: 100%;
  font-size: 1.02rem;
  background: white;
}
.Organ-requests-table th, .Organ-requests-table td {
  padding: 0.65rem 0.5rem;
  border: 1px solid var(--border-light, #e5e7eb);
  text-align: left;
}
.Organ-requests-table thead {
  background: var(--bg-gray-100, #f3f4f6);
}
.urgency-pill {
  border-radius: var(--radius-xl, 12px);
  color: #fff;
  font-weight: 600;
  padding: 0.23rem 0.9rem;
  font-size: 0.97rem;
  background: var(--accent, #3b82f6);
  display: inline-block;
}
.status-badge {
  border-radius: var(--radius-lg, 8px);
  background: #eee;
  font-weight: 500;
  color: #444;
  padding: 0.23rem 0.7rem;
  font-size: 0.95rem;
}
.status-pending {
  background: #fee2e2;
  color: #b91c1c;
}
.status-cancelled {
  background: #f5f3ff;
  color: #7c3aed;
}
.status-fulfilled {
  background: #dcfce7;
  color: #22c55e;
}
@media (max-width: 900px) {
  .Organ-request-dashboard-container {
    padding: 1rem 0.2rem;
  }
}
@media (max-width: 600px) {
  .Organ-requests-table, .Organ-requests-table thead, .Organ-requests-table tbody, .Organ-requests-table th, .Organ-requests-table td, .Organ-requests-table tr {
    display: block;
  }
  .Organ-requests-table thead {
    display: none;
  }
  .Organ-requests-table tr {
    background: var(--bg-gray-100, #f3f4f6);
    margin-bottom: 0.85rem;
    box-shadow: var(--shadow-sm, 0 1px 2px rgba(0,0,0,0.05));
    border-radius: var(--radius-lg, 8px);
    padding: 0.5rem;
  }
  .Organ-requests-table td {
    border: none;
    padding: 0.7rem 0.7rem;
    text-align: left;
    display: flex;
    flex-direction: row;
    gap: 0.85rem;
    align-items: center;
  }
}
