.Organ-availability-container {
  max-width: 680px;
  margin: 2rem auto;
  background: var(--bg-white, #fff);
  border-radius: var(--radius-xl, 12px);
  box-shadow: var(--shadow-md, 0 4px 6px rgba(0,0,0,0.1));
  padding: 2rem 1.2rem 1rem 1.2rem;
}
.availability-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 1.4rem;
}
.availability-header h2 {
  color: var(--accent, #3b82f6);
  font-size: 1.4rem;
}
.availability-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 1.7rem 1.4rem;
  justify-content: space-around;
  margin-bottom: 1.2rem;
}
.availability-card {
  flex-basis: 130px;
  background: var(--bg-gray-50, #f9fafb);
  border-radius: var(--radius-md, 6px);
  box-shadow: var(--shadow-sm, 0 1px 2px rgba(0,0,0,0.05));
  padding: 1rem 0.7rem 0.9rem 0.7rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 110px;
  margin-bottom: 0.9rem;
  border: 1px solid var(--border-light, #e5e7eb);
  position: relative;
}
.Organtype-label {
  font-size: 1.14rem;
  font-weight: 600;
  margin-bottom: 0.25rem;
  letter-spacing: 0.02em;
}
.progress-bar-outer {
  width: 95%;
  height: 13px;
  background: #e5e7eb;
  border-radius: 12px;
  margin-bottom: 0.4rem;
  margin-top: 0.3rem;
  overflow: hidden;
}
.progress-bar-inner {
  height: 100%;
  border-radius: 12px;
  transition: width 0.5s;
}
.availability-value {
  font-weight: 700;
  font-size: 1.02rem;
}
.availability-value.low {
  color: #dc2626;
}
.low-indicator {
  background: #dc2626;
  color: white;
  padding: 0.08rem 0.7rem;
  border-radius: 12px;
  font-size: 0.89rem;
  margin-top: 0.35rem;
  font-weight: 500;
}
.last-updated {
  margin-top: 1.2rem;
  text-align: right;
  color: var(--text-muted, #9ca3af);
  font-size: 0.93rem;
}
@media (max-width: 700px) {
  .availability-grid {
    flex-direction: column;
    align-items: center;
    gap: 1rem 0;
  }
  .availability-card {
    min-width: 160px;
    width: 98%;
  }
}
